<template>
  <div id="photo_sharing">
    <g-header/>
    <van-tabs @click="getImgList">
      <van-tab title="全部">
        <van-row v-for="(item,index) in imgList" :key="index">
          <van-col span="22" offset="1" @click="goImgDetails(item.id)">
            <img v-lazy="item.img_url" alt class="sharingImg" />
            <div class="introductory">
              <span>{{item.title}}</span>
              <p>{{item.zhaiyao}}</p>
            </div>
          </van-col>
        </van-row>
      </van-tab>
      <van-tab v-for="item in imgCategoriesList" :title="item.title" :key="item.id">
        <van-row v-for="(item) in currentImgList" :key="item.id">
          <van-col span="22" offset="1" @click="goImgDetails(item.id)">
            <img :src="item.img_url" alt class="sharingImg" />
            <div class="introductory">
              <span>{{item.title}}</span>
              <p>{{item.zhaiyao}}</p>
            </div>
          </van-col>
        </van-row>
      </van-tab>
    </van-tabs>
    <g-tabbar/>
  </div>
</template>

<script>
import GHeader from '../header'
import GTabbar from '../tabbar'
export default {
  data () {
    return {
      // 图片分类列表
      imgCategoriesList: [],
      // 当前分类图片列表
      currentImgList: [],
      // 全部图片列表
      imgList: []
    }
  },
  components: {
    GHeader,
    GTabbar
  },
  created () {
    this.getImgCategory()
  },
  methods: {
    // 获取图片分类列表
    async getImgCategory () {
      const res = await this.$http.get('/api/getimgcategory')
      // console.log(res)
      if (res.status !== 0) this.$toast.fail('导航信息获取失败')
      this.imgCategoriesList = res.message
      // console.log(this.imgCategoriesList)
      this.getAllImgList()
    },
    // 获取分类图片的列表
    async getImgList (index, title) {
      if (index === 0) return
      const id = this.imgCategoriesList[index - 1].id

      const res = await this.$http.get(`/api/getimages/${id}`)
      // console.log(data)
      if (res.status !== 0) this.$toast.fail('图片信息获取失败')
      this.currentImgList = res.message
      // console.log(this.currentImgList)
    },
    // 前往图片详情页
    goImgDetails (id) {
      this.$router.push('/photo_details/' + id)
    },
    // 获取全部图片
    getAllImgList () {
      // console.log(this.imgCategoriesList)

      this.imgCategoriesList.forEach(async item => {
        const res = await this.$http.get(`/api/getimages/${item.id}`)
        // console.log(res)
        if (res.status !== 0) this.$toast.fail('图片信息获取失败')
        this.imgList.push(...res.message)
      })
      // console.log(this.imgList)
    }
  },
  mounted () {}
}
</script>

<style lang="less" scoped>
.sharingImg {
  width: 100% !important;
  margin-top: 10px;
  border-radius: 10px;
  box-shadow: 1px 0 8px 1px rgba(0, 0, 0, 0.3);
}
.van-col {
  position: relative;
}
#photo_sharing {
  margin-bottom: 50px;
}
.introductory {
  position: absolute;
  bottom: 0;
  background-color: rgba(61, 58, 58, 0.5);
  border-radius: 0 0 10px 10px;
  color: #fff;
}
</style>
