<template>
  <div id="photo_details">
    <!-- 标题 -->
    <g-header />
    <van-row>
      <van-col span="24" offset="2" class="imageInfoTitle">
        <h5>{{imageInfo.title}}</h5>
      </van-col>
    </van-row>
    <!-- 时间和点击数 -->
    <van-row type="flex" justify="space-between" style="color: #9D9DC1">
      <van-col span="16">发表时间:{{imageInfo.add_time | dateformat('YYYY-MM-DD HH:mm:ss')}}</van-col>
      <van-col span="4">点击:{{imageInfo.click}}</van-col>
    </van-row>
    <hr class="interval" />
    <!-- 预览缩量略图 -->
    <div class="thumimages">
      <ul>
        <li v-for="(item, index) in imageList" :key="index">
          <img :src="item.src" alt @click="showImg" />
        </li>
        <li v-if="imageList.length%3 == 2"></li>
        <template v-else-if="imageList.length%3 == 1">
          <li></li>
          <li></li>
        </template>
      </ul>
    </div>
    <!-- 文章内容 -->
    <p v-html="imageInfo.content"></p>
    <h3>发表评论</h3>
    <hr class="interval" />
    <van-cell-group>
      <van-field v-model="message" rows="5" autosize type="textarea" placeholder="请输入留言" />
    </van-cell-group>
    <van-row>
      <van-button type="info" style="width: 100%" @click="testComment">发表评论</van-button>
    </van-row>
    <!-- 评论列表 -->
    <van-panel v-for="(item, index) in comments" :key="index">
      <p>第{{index+1}}楼 用户:{{item.user_name}} 发表时间:{{item.add_time | dateformat('YYYY-MM-DD HH:mm:ss')}}</p>
      <div class="text-indent">{{item.content}}</div>
    </van-panel>
    <van-button plain hairline type="warning" style="width: 100%" @click="getMore">加载更多</van-button>
    <br />
    <g-tabbar />
  </div>
</template>

<script>
import { ImagePreview } from 'vant'
import GHeader from '../header'
import GTabbar from '../tabbar'

export default {
  props: ['id'],
  data () {
    return {
      // 当前图片详细数据
      imageInfo: {},
      // 当前图片的缩略图表
      imageList: [],
      // 留言内容
      message: '',
      // 评论数据
      comments: [],
      // 评论页数
      pageindex: 1,
      // 预览配置
      priewConfig: {
        images: [],
        closeable: true
      }
    }
  },
  components: {
    GHeader,
    GTabbar
  },
  created () {
    this.getImageInfo()
    this.getImageList()
    this.getComments()
  },
  methods: {
    // 获取对应图片的详细数据 /api/getimageInfo/:imgid
    async getImageInfo () {
      const res = await this.$http.get(`/api/getimageInfo/${this.id}`)
      if (res.status !== 0) return this.$toast.fail('获取图片数据失败')
      // console.log(res)
      this.imageInfo = res.message[0]
    },
    // 获取对应图片的缩略图表
    async getImageList () {
      const res = await this.$http.get(`/api/getthumimages/${this.id}`)
      if (res.status !== 0) return this.$toast.fail('获取图片数据失败')
      // console.log(res)
      this.imageList = res.message
      this.imageList.forEach(item => this.priewConfig.images.push(item.src))
    },
    // 获取评论列表/ api/getcomments/:artid?pageindex=1
    async getComments () {
      const res = await this.$http.get(
        `/api/getcomments/${this.id}?pageindex=${this.pageindex}`
      )
      // console.log(res)
      if (res.status !== 0) return this.$toast.fail('获取评论数据失败')
      this.comments = res.message
    },
    // 发表评论前验证
    testComment () {
      if (this.message.trim().length === 0) { return this.$toast.fail('留言不能为空') }
      this.pageindex = 1
      this.postComment()
    },
    // 发表评论 /api/postcomment/:artid
    async postComment () {
      const res = await this.$http.post(`/api/postcomment/${this.id}`, {
        content: this.message
      })
      // console.log(res)
      if (res.status !== 0) return this.$toast.fail('评论失败')
      this.getComments()
      this.message = ''
    },
    // 加载更多评论
    getMore () {
      this.pageindex += 1
      this.getComments()
    },
    // 预览图片
    showImg () {
      ImagePreview(this.priewConfig)
    }
  }
}
</script>

<style lang="less" scoped>
#photo_details {
  margin-bottom: 50px;
  padding: 0 5px 0;
}
.imageInfoTitle {
  text-align: center;
  // font-size: 14px;
  color: #26a2ff;
  margin-left: 0 !important;
}
.interval {
  margin: 15px 0;
}
.thumimages ul {
  display: flex;
  width: 100%;
  justify-content: space-around;
  flex-wrap: wrap;
  li {
    width: 30%;
    img {
      width: 100%;
      border-radius: 5px;
      box-shadow: 1px 0 8px 1px rgba(0, 0, 0, 0.3);
    }
  }
}
.van-field {
  border: 1px solid #dcdcdc;
}
.van-button {
  margin: 7px 0;
  font-weight: 700;
}
.van-panel {
  p {
    height: 30px;
    background-color: #ccc;
    font-style: 12px;
    line-height: 30px;
  }
  .text-indent {
    text-indent: 2em;
  }
  .van-panel__header {
    padding: 0 !important;
  }
}
</style>
